* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.cont-temporizador {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.cont-temporizador .bloque {
  color: #fff;
  margin: 0px 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cont-temporizador .bloque p {
  color: #fff;
}
.cont-temporizador .bloque div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 6px 2px #727272 inset;
  color: #fff;
  font-size: 40px;
  font-weight: bold;
  width: 80px;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 5px;
}